<template>
  <div id="app">
    <div id="nav">
      <router-link to="/">Home</router-link> |
      <a href="" v-if="loggedin" @click.prevent="signOut">Sign out</a>
      <a v-else href="" @click.prevent="signIn">Sign in / Sign up</a>
    </div>
    <router-view />
  </div>
</template>

<script>
import firebase from "firebase";

export default {
  name: "App",
  data() {
    return {
      loggedin: false,
    };
  },
  methods: {
    signIn() {
      firebase
        .auth()
        .signInWithPopup(new firebase.auth.GoogleAuthProvider())
        .then(() => {
          if (firebase.auth().currentUser) {
            this.loggedin = true;
          } else {
            this.loggedin = false;
          }
        });
    },
    signOut() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          if (firebase.auth().currentUser) {
            this.loggedin = true;
          } else {
            this.loggedin = false;
          }
        });
      if (this.$route.name != "Home") {
        this.$router.push({ name: "Home" });
      }
      this.$notify.success({
          title: 'You have successfully signed out.',
          message: 'Give us an A. Please!',
          position:'top-left',
          showClose: false
        });
    },
    refresh() {
      if (firebase.auth().currentUser) {
        this.loggedin = true;
      } else {
        this.loggedin = false;
      }
    },
  },
  created() {
    this.interval = setInterval(() => this.refresh(), 1000);
  },
};
</script>

