<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <div class="dropdown">
      <el-collapse v-model="activeNames">
        <el-collapse-item title="Players, README!" name="1">
          <div>
            You will be given a 30 seconds clip of a song, and you will need to
            choose the correct song name along with other 3 incorrect anwsers.
            Good luck!
          </div>
          <div class="buttons">
            <el-button type="primary" plain @click="checkStatusP">
              <!-- <router-link to="/playground">Let's Go!</router-link> -->
              Let's Go
            </el-button>
          </div>
        </el-collapse-item>
        <el-collapse-item title="Admins, README!" name="2">
          <div>
            Administrators have the privilege to add to or delete songs from our
            database.
          </div>
          <div>However, only the special ones can be the administrators.</div>
          <div>
            Here is a <span id="cross">SECRET</span> place for administrators to
            sign in. DO NOT even attempt to sign in here if you are not the
            administrator!
          </div>
          <div class="buttons">
            <el-button type="warning" plain @click="checkStatusA"
              >Admins Only</el-button
            >
          </div>
        </el-collapse-item>
        <el-collapse-item title="Our Team" name="3">
          <div>
            Backend: Xu Chen (xuc323); <br />Database: Zheyan Chen (zhc222);
            <br />
            Frontend: Thomas Liu (xil322); <br />
            Backend: Wei Zheng (wez223)
          </div>
        </el-collapse-item>
      </el-collapse>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";

export default {
  name: "HelloWorld",
  props: {
    msg: String,
  },
  data() {
    return {
      activeNames: [],
      userStatus: 0,
    };
  },
  methods: {
    checkStatusP() {
      if (firebase.auth().currentUser) {
        //redirect here
        this.$router.push({ name: "Playground" });
        return;
      }
      this.$notify.error({
        title: "Only Privileged Ones Can Access Our Game",
        message: "Sign in or sign up with Google in the top right corner",
        position: "top-left",
        showClose: false,
      });
    },
    checkStatusA() {
      if (firebase.auth().currentUser) {
        //if the person is an admin
        // console.log(firebase.auth().currentUser.uid);
        var temp = { uid: firebase.auth().currentUser.uid };
        this.$http
          .post(
            process.env.BASE_URL + "users/adminsignin",
            JSON.stringify(temp),
            { emulateJSON: true }
          )
          .then((res) => {
            if (res.status == 200) {
              this.$router.push({ name: "Admin" });
            }
          })
          .catch((error) => {
            this.$notify.error({
              title: "Not Authorized",
              message:
                "If you believe this is an error, please contact the team",
              position: "top-left",
              showClose: false,
            });
          });
      } else {
        this.$notify.error({
          title: "Please Sign in",
          message: "Sign in or sign up with Google in the top right corner",
          position: "top-left",
          showClose: false,
        });
      }
    },
  },
};
</script>

